@import './../../styles/index.scss';

.button {
  color: $c-white;
  background-color: $c-text;
  width: 238px;
  height: 56px;
  border-radius: $border-radius;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 24px;
  padding-right: 24px;
  border: none;

  @media screen and (max-width: $between) {
    padding-left: 18px;
    padding-right: 18px;
    width: 210px;
    height: 46px;
  }
}

.text {
  font-size: 14px;
  letter-spacing: 1.25px;
}

.dot {
  width: 24px;
  height: 24px;
  background-color: $c-dot;
  border-radius: $border-radius;

  @media screen and (max-width: $between) {
    width: 20px;
    height: 20px;
  }
}
