@import './../../styles/index.scss';

.contactsWrapper {
  padding-top: 80px;
}

.contactContainer {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 30px;
  padding-top: 80px;

  @media screen and (max-width: $phone) {
    flex-direction: column;
    align-items: center;
  }
}

.description {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 550px;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    max-width: 400px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    max-width: 340px;
  }

  @media screen and (max-width: $tablet) {
    max-width: 220px;
  }
}

.littleTitleContainer {
  display: flex;
  gap: 16px;
  margin-bottom: 80px;

  @media screen and (max-width: $phone) {
    margin-left: auto;
    margin-right: auto;
  }
}

.iconContainer {
  width: 12px;
  height: 12px;
}

.littleTitle {
  letter-spacing: 0.15px;
  line-height: 140%;
}

.title {
  font-weight: $fw-regular;
  font-size: 96px;
  line-height: 110%;
  letter-spacing: -1.5px;
  margin-bottom: 80px;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    font-size: 70px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    font-size: 50px;
  }

  @media screen and (max-width: $tablet) {
    font-size: 36px;
  }
}

.italic {
  position: relative;
  font-style: italic;
}

.circleIconContainer {
  position: absolute;
  top: 4px;
  right: 0;
  left: 1px;
  width: 394px;
  height: 100px;
  z-index: -1;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    width: 320px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    width: 240px;
    left: -5px;
    top: -15px;
  }

  @media screen and (max-width: $tablet) {
    width: 170px;
    left: -5px;
    top: -25px;
  }
}

// ! Contacts

.contactTitle {
  letter-spacing: 0.15px;
  line-height: 140%;

  @media screen and (max-width: $tablet) {
    font-size: 14px;
  }

  @media screen and (max-width: $phone) {
    text-align: center;
  }
}

.contactDescription {
  font-size: 34px;
  letter-spacing: 0.25px;
  line-height: 120%;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    font-size: 26px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    font-size: 22px;
  }

  @media screen and (max-width: $tablet) {
    font-size: 16px;
  }

  @media screen and (max-width: $phone) {
    text-align: center;
  }
}

.contact {
  margin-bottom: 80px;
}
