@import './../../styles/index.scss';

.testimonials {
  margin-bottom: 120px;
  padding-top: 120px;
}

.swiper {
  padding-left: 120px;

  @media screen and (min-width: 1700px) and (max-width: 1800px) {
    padding-left: 70px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1700px) {
    padding-left: 40px;
  }

  @media screen and (max-width: $tablet) {
    padding-left: 20px;
  }
}

.littleTitleContainer {
  display: flex;
  gap: 16px;
  margin-bottom: 80px;
}

.iconContainer {
  width: 12px;
  height: 12px;
}

.littleTitle {
  letter-spacing: 0.15px;
  line-height: 140%;
}

// ! Slider

.text {
  font-size: 96px;
  line-height: 110%;
  letter-spacing: -1.5px;
  margin-bottom: 48px;

  @media screen and (min-width: $between) and (max-width: 1700px) {
    font-size: 76px;
  }

  @media screen and (min-width: $desktop) and (max-width: $between) {
    font-size: 44px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    font-size: 36px;
  }

  @media screen and (min-width: 630px) and (max-width: $tablet) {
    font-size: 28px;
    margin-bottom: 30px;
  }

  @media screen and (min-width: $phone) and (max-width: 630px) {
    font-size: 24px;
    margin-bottom: 30px;
  }

  @media screen and (min-width: 400px) and (max-width: $phone) {
    font-size: 22px;
  }

  @media screen and (max-width: 400px) {
    font-size: 18px;
    margin-bottom: 20px;
  }
}

.imageContainer {
  width: 80px;
  flex-basis: 80px;
  flex-shrink: 0;
  flex-grow: 0;
  height: 120px;
  border-radius: 64px;
}

.imageContainer img {
  border-radius: 64px;
}

.nameContainer {
  display: flex;
  align-items: center;
  gap: 40px;

  @media screen and (max-width: $tablet) {
    gap: 20px;
  }
}

.name {
  font-size: 34px;
  line-height: 120%;
  letter-spacing: 0.25px;

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    font-size: 28px;
  }

  @media screen and (min-width: 630px) and (max-width: $tablet) {
    font-size: 22px;
  }

  @media screen and (min-width: $phone) and (max-width: 630px) {
    font-size: 18px;
  }
  @media screen and (max-width: $phone) {
    font-size: 16px;
  }
}

.lineOne {
  position: absolute;
  z-index: -1;
  top: 0;
  left: -10px;

  @media screen and (max-width: $desktop) {
    left: -5px;
  }
}

.lineTwo {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 47px;

  @media screen and (min-width: $desktop) and (max-width: $between) {
    left: 25px;
  }

  @media screen and (max-width: $desktop) {
    left: 18px;
  }
}
