@import './../../styles/index.scss';

.selected {
  padding-top: 120px;

  @media screen and (max-width: 1050px) {
    padding-top: 50px;
  }
}

.littleTitleContainer {
  display: flex;
  gap: 16px;
  margin-bottom: 80px;

  @media screen and (max-width: 1050px) {
    margin-bottom: 50px;
    justify-content: center;
  }
}

.iconContainer {
  width: 12px;
  height: 12px;
}

.littleTitle {
  letter-spacing: 0.15px;
  line-height: 140%;
}

.descriptionGroup {
  width: 100%;
  max-width: 824px;

  @media screen and (min-width: $screen) and (max-width: 1630px) {
    max-width: 600px;
  }

  @media screen and (min-width: 1050px) and (max-width: $screen) {
    max-width: 450px;
  }

  @media screen and (max-width: 1050px) {
    max-width: 600px;
    margin-bottom: 70px;
  }
}

.description {
  font-size: 96px;
  line-height: 110%;
  letter-spacing: -1.5px;

  @media screen and (min-width: $screen) and (max-width: 1630px) {
    font-size: 66px;
  }

  @media screen and (max-width: $screen) {
    font-size: 46px;
  }

  @media screen and (max-width: $phone) {
    font-size: 36px;
    text-align: center;
  }
}

.info {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;

  margin-bottom: 8px;
}

.name {
  font-size: 24px;
  line-height: 140%;
}

.buttonGroup {
  width: 100%;
  max-width: 253px;

  @media screen and (max-width: 950px) {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.buttonGroupText {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  margin-top: 32px;
  margin-bottom: 32px;

  @media screen and (max-width: 950px) {
    font-size: 18px;
    text-align: center;
  }
}

// ! Images

.imageContainerFirst {
  width: 100%;
  max-width: 681px;
  margin-bottom: 24px;

  @media screen and (max-width: 1230px) {
    max-width: 500px;
  }

  @media screen and (max-width: 1050px) {
    max-width: 100%;
  }
}

.imageContainerSecond {
  width: 100%;
  margin-bottom: 24px;
  max-width: 538px;

  @media screen and (max-width: 1400px) {
    max-width: 400px;
  }

  @media screen and (max-width: 1050px) {
    max-width: 100%;
  }
}

.imageContainerThird {
  width: 100%;
  margin-bottom: 24px;
  max-width: 967px;

  @media screen and (max-width: 1500px) {
    max-width: 700px;
  }

  @media screen and (max-width: 680px) {
    max-width: 100%;
  }
}

.imageContainerThird img {
  @media screen and (max-width: 680px) {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 600px;
    object-position: -200px;
  }
}

.imageContainerFourth {
  width: 100%;
  margin-bottom: 24px;
  max-width: 396px;
}

.imageContainerFourth img {
      @media screen and (max-width: 400px) {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 400px;
  }
}

.imageContainerFifth {
  width: 100%;
  margin-bottom: 24px;
  max-width: 396px;

  @media screen and (max-width: 950px) {
    max-width: 100%;
  }
}

.imageContainerSixth {
  width: 100%;
  margin-bottom: 24px;
  max-width: 396px;

  @media screen and (max-width: 950px) {
    max-width: 100%;
  }
}

.imageContainerSeventh {
  width: 100%;
  margin-bottom: 24px;
  max-width: 824px;

  @media screen and (min-width: 1530px) and (max-width: 1750px) {
    max-width: 600px;
  }

  @media screen and (max-width: 1530px) {
    max-width: 450px;
  }

  @media screen and (max-width: 950px) {
    max-width: 100%;
  }
}

// ! Infos containers
.infosContainerFirst,
.infosContainerSecond,
.infosContainerThird,
.infosContainerFourth,
.infosContainerFifth,
.infosContainerSixth,
.infosContainerSeventh {
  position: absolute;
  // display: none;
}

.infosContainerFirst {
  top: 0;
  left: 0;

  @media screen and (max-width: 1050px) {
    position: static;
    height: 100%;
    margin-bottom: 40px;
  }
}
.infosContainerSecond {
  bottom: 0;
  right: 143px;

  @media screen and (max-width: 1630px) {
    right: 40px;
  }

  @media screen and (max-width: 1050px) {
    position: static;
    height: 100%;
  }
}
.infosContainerThird {
  top: 0;
  left: 0;
}
.infosContainerFourth {
  right: 0;
  bottom: 0;
}
.infosContainerFifth {
  top: 0;
  left: 0;

  @media screen and (max-width: 950px) {
    position: static;
    height: 100%;
    width: 100%;
    margin-bottom: 40px;
  }
}
.infosContainerSixth {
  bottom: 0;
  left: 428px;

  @media screen and (max-width: 950px) {
    position: static;
    height: 100%;
    width: 100%;
    margin-bottom: 40px;
  }
}
.infosContainerSeventh {
  top: 0;
  right: 0;

  @media screen and (max-width: 950px) {
    position: static;
    height: 100%;
    margin-bottom: 40px;
  }
}

// ! First Container

.firstContainer {
  width: 100%;
  height: 1183px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 175px;

  @media screen and (max-width: 1230px) {
    height: 900px;
  }

  @media screen and (max-width: 1050px) {
    height: 100%;
    align-items: center;
  }

  @media screen and (max-width: $tablet) {
    margin-bottom: 40px;
  }
}

// !Second container
.secondContainer {
  width: 100%;
  height: 651px;
  position: relative;
  margin-bottom: 175px;

  @media screen and (min-width: 680px) and (max-width: 1200px) {
    height: 970px;
  }

  @media screen and (max-width: 950px) {
    margin-bottom: 40px;
  }

  @media screen and (max-width: 681px) {
    height: 1200px;
  }
}

// ! Third container

.thirdContainer {
  width: 100%;
  height: 935px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: end;
  margin-bottom: 120px;

  @media screen and (max-width: 1530px) {
    height: 1070px;
  }

  @media screen and (max-width: 950px) {
    height: 100%;
    align-items: center;
    margin-bottom: 20px;
  }
}
