@import './../../styles/index.scss';

.ourProcess {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  @media screen and (max-width: $screen) {
    flex-direction: column;
    align-items: center;
  }
}

.nameContainer {
  display: flex;
  gap: 16px;

  @media screen and (max-width: $screen) {
    margin-bottom: 60px;
  }
}

.iconContainer {
  width: 12px;
  height: 12px;
}

.name {
  line-height: 140%;
  letter-spacing: 0.15px;
}

// ! Content Container
.container {
  width: 100%;
  max-width: 1110px;
}

.content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.line {
  width: 100%;
  margin-bottom: 64px;
}

.card {
  width: 100%;
  max-width: 888px;
  margin-bottom: 48px;
}

.number {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 24px;
  line-height: 140%;
  color: $c-dot;

  @media screen and (max-width: $phone) {
    font-size: 18px;
  }
}

.dotsIconContainer {
  position: absolute;
  top: 0;
  right: 0;
  background-color: $c-text;
  width: 48px;
  height: 48px;
  border-radius: 72px;
  padding: 14px 22px;

  @media screen and (max-width: $phone) {
    width: 40px;
    height: 40px;
    padding: 10px 10px;
    border-radius: 50px;
  }
}

.titleContainer {
  display: flex;
  justify-content: flex-start;

  @media screen and (max-width: 1084px) {
    justify-content: center;
  }
}

.title {
  font-weight: $fw-regular;
  font-size: 24px;
  line-height: 140%;
  margin-bottom: 32px;

  @media screen and (max-width: $phone) {
    font-size: 18px;
  }
}

.description {
  font-size: 48px;
  line-height: 120%;
  margin-bottom: 32px;

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    font-size: 36px;
  }

    @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 26px;
  }

  @media screen and (max-width: $phone) {
    font-size: 22px;
  }
}

.textContainer {
  background-color: $c-bg;
  padding: 32px;
  border-radius: 8px;

    @media screen and (max-width: $phone) {
    padding: 28px 18px;
  }
}

.text {
  font-size: 24px;
  line-height: 140%;
  margin-bottom: 64px;

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    font-size: 18px;
  }

  @media screen and (max-width: $phone) {
    font-size: 14px;
    margin-bottom: 32px;
  }
}
