@import './../../styles/index.scss';

.icon {
  width: 100%;
  height: 100%;
  fill: $c-text;
  transition: $t-normal;
  cursor: pointer;
}

.icon-cross {
  transition: $t-normal;
  fill: $c-white;

  &:hover {
    fill: $c-dot;
  }
}

.icon-circle,
.icon-circle-2,
.icon-circle-3,
.icon-circle-4 {
  fill: transparent;
  stroke: $c-dot;
}

.icon-dots {
  fill: $c-white;
}

.icon-chat-bubble {
  fill: $c-text;
  stroke: $c-text;
}

.icon-line {
  width: 90px;
  height: 90px;
  fill: rgba($c-dot, 0.7);

  @media screen and (min-width: $desktop) and (max-width: $between) {
    width: 60px;
    height: 60px;
  }

  @media screen and (max-width: $desktop) {
    width: 40px;
    height: 40px;
  }
}
