@import './../../styles/index.scss';

// ! title
.title {
  width: 100%;
  font-size: 133px;
  line-height: 110%;
  letter-spacing: -1.5px;
  font-weight: $fw-regular;
  text-align: center;
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 56px;
  margin-bottom: 56px;

  @media screen and (min-width: 1360px) and (max-width: 1760px) {
    font-size: 100px;
  }

  @media screen and (min-width: 1000px) and (max-width: 1360px) {
    font-size: 70px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1000px) {
    font-size: 56px;
  }

  @media screen and (min-width: 570px) and (max-width: $tablet) {
    font-size: 40px;
  }

  @media screen and (min-width: 400px) and (max-width: 570px) {
    font-size: 26px;
    gap: 12px;
  }

  @media screen and (max-width: 400px) {
    font-size: 20px;
    gap: 8px;
  }
}

.span {
  position: relative;
  z-index: 5;
  font-style: italic;
  font-weight: $fw-regular;
}

.iconContainer {
  top: 0;
  width: 300px;
  height: 136px;
  left: -19px;
  top: 4px;
  position: absolute;
  z-index: -1;

  @media screen and (min-width: 1360px) and (max-width: 1760px) {
    width: 224px;
    left: -12px;
    top: 0;
  }

  @media screen and (min-width: 1000px) and (max-width: 1360px) {
    width: 160px;
    left: -12px;
    top: -20px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1000px) {
    width: 125px;
    left: -12px;
    top: -30px;
  }

  @media screen and (min-width: 570px) and (max-width: $tablet) {
    width: 90px;
    top: -40px;
    left: -10px;
  }

  @media screen and (min-width: 400px) and (max-width: 570px) {
    width: 64px;
    top: -50px;
    left: -10px;
  }

  @media screen and (max-width: 400px) {
    width: 50px;
    top: -55px;
    left: -8px;
  }
}

// ! Content
.contentContainer {
  margin-top: 120px;
  margin-bottom: 200px;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: $between) {
    margin-bottom: 100px;
  }

  @media screen and (max-width: $desktop) {
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
  }
}

.nameContainer {
  display: flex;
  gap: 16px;

  @media screen and (max-width: $desktop) {
    margin-bottom: 20px;
  }
}

.squareIconContainer {
  width: 12px;
  height: 12px;
}

.name {
  font-weight: $fw-medium;
  line-height: 140%;
  letter-spacing: 0.15px;
}

.content {
  width: 100%;
  max-width: 1110px;

  @media screen and (min-width: $between) and (max-width: $screen) {
    max-width: 880px;
  }

  @media screen and (max-width: $between) {
    max-width: 700px;
  }
}

.contentTitle {
  font-weight: $fw-regular;
  font-size: 60px;
  line-height: 120%;
  letter-spacing: -0.5px;
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;

  @media screen and (min-width: $between) and (max-width: $screen) {
    font-size: 46px;
  }

  @media screen and (max-width: $between) {
    font-size: 36px;
  }

  @media screen and (min-width: 490px) and (max-width: $tablet) {
    margin-bottom: 30px;
  }

  @media screen and (max-width: 490px) {
    font-size: 24px;
    margin-bottom: 30px;
  }
}

.contentTitle span:first-of-type {
  align-self: flex-start;

  @media screen and (max-width: 490px) {
    align-self: center;
  }
}

.contentTitle span:last-of-type {
  align-self: flex-end;

  @media screen and (max-width: 490px) {
    align-self: center;
  }
}

.textContainer {
  width: 100%;
  max-width: 1110px;
  position: relative;
  display: flex;
  gap: 32px;
  align-items: flex-start;

  @media screen and (min-width: 370px) and (max-width: 550px) {
    flex-direction: column;
    height: 250px;
  }

  @media screen and (max-width: 370px) {
    flex-direction: column;
    height: 280px;
  }
}

.buttonContainer {
  position: absolute;
  bottom: 0;
  left: 0;
}

.text {
  flex-basis: 539px;
  font-size: 24px;
  line-height: 140%;

  @media screen and (min-width: $between) and (max-width: $screen) {
    font-size: 18px;
  }

  @media screen and (min-width: $tablet) and (max-width: $between) {
    font-size: 16px;
  }

  @media screen and (min-width: 550px) and (max-width: $tablet) {
    font-size: 14px;
  }

  @media screen and (max-width: 550px) {
    flex-basis: 0;
    font-size: 14px;
  }
}
