@import './../../styles/index.scss';

// ! Header wrapper

.headerWrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

// ! Header

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
}

.navContainer {
  @media screen and (max-width: $desktop) {
    display: none;
  }
}

.nav {
  display: flex;
  gap: 30px;
}

.link,
.linkVertical {
  font-weight: $fw-medium;
  font-size: 20px;
  letter-spacing: 0.15px;
  line-height: 120%;
  transition: $t-normal;

  @media screen and (max-width: $between) {
    font-size: 16px;
  }

    &:hover {
    color: $c-dot;
  }
}

.buttonGroup {
  display: flex;
  gap: 32px;
  align-items: center;

  @media screen and (max-width: $desktop) {
    display: none;
  }
}

.iconContainer {
  width: 24px;
  height: 24px;
}

// ! Mobile menu

.mobileMenu {
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $c-mobile-bg;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navVertical {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.linkVertical {
  color: $c-white;
  padding: 12px 50px;
  transition: $t-normal;

  &:hover {
    color: $c-dot;
  }
}

.cross {
  display: none;
}

.crossVisible {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: 20px;
  right: 20px;
}

.burger {
  width: 24px;
  height: 24px;

  @media screen and (min-width: $desktop) {
    display: none;
  }
}
