@import './../../styles/index.scss';

.services {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-bottom: 120px;

  @media screen and (max-width: $desktop) {
    grid-template-columns: 1fr;
  }
}

.card {
  width: 100%;
  padding: 64px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid $c-text;
  border-bottom: 1px solid $c-text;
  border-right: 1px solid $c-text;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    padding: 54px 22px;
  }

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    width: 100%;
    margin: 0 auto;
    max-width: 700px;
    border: none;
    padding: 54px 22px;
  }

  @media screen and (max-width: $phone) {
    margin: 0 auto;
    max-width: 700px;
    border: none;
    padding: 30px 15px;
  }
}

.card:last-of-type {
  border-right-color: transparent;
}

.pretitle {
  font-size: 20px;
  font-weight: $fw-medium;
  line-height: 120%;
  letter-spacing: 0.15px;
  margin-bottom: 40px;

  @media screen and (max-width: $desktop) {
    margin-bottom: 20px;
  }
}

.title,
.titleFirst {
  width: 100%;
  max-width: 570px;
  text-align: center;
  font-size: 60px;
  font-weight: $fw-regular;
  line-height: 120%;
  letter-spacing: -0.5px;
  margin-bottom: 64px;

  @media screen and (min-width: $screen) and (max-width: $large) {
    max-width: 520px;
    font-size: 42px;
  }

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    font-size: 30px;
  }

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    max-width: 700px;
    font-size: 34px;
    margin-bottom: 20px;
  }

  @media screen and (max-width: $phone) {
    max-width: 700px;
    font-size: 28px;
    margin-bottom: 10px;
  }
}

.titleFirst {
  max-width: 430px;

  @media screen and (max-width: $desktop) {
    max-width: 700px;
  }
}

.imageContainer {
  width: 100%;
  max-width: 360px;
  margin-bottom: 64px;
  border-radius: 8px;

  @media screen and (max-width: $phone) {
    margin-bottom: 40px;
  }
}

.imageContainer img {
  border-radius: 8px;
}
