@import './../../styles/index.scss';

// ! Image
.imageBlock {
  position: relative;
  margin-top: 80px;
  margin-bottom: 16px;
}

.imageContainer img {
  @media screen and (max-width: $tablet) {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 500px;
  }
}

.notice {
  position: absolute;
  font-weight: $fw-medium;
  top: 34px;
  left: 40px;
  padding: 2px 8px;
  border-radius: 48px;
  font-size: 16px;
  letter-spacing: 0.15px;
  line-height: 140%;
  background-color: $c-white;

  @media screen and (max-width: $desktop) {
    top: 20px;
    left: 16px;
    font-size: 14px;
  }
}

// ! Description
.descriptionBlock {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: 80px;

  @media screen and (max-width: $desktop) {
    flex-direction: column;
    gap: 10px;
  }
}

.description {
  width: 100%;
  max-width: 253px;
  font-weight: $fw-medium;
  letter-spacing: 0.15px;
  line-height: 140%;

  @media screen and (max-width: $desktop) {
    font-size: 14px;
  }
}

.description:first-of-type {
  display: flex;
  gap: 16px;

  @media screen and (max-width: $desktop) {
    align-self: flex-start;
  }
}

.description:nth-of-type(2) {
  align-self: center;
}

.description:nth-of-type(3) {
  align-self: flex-end;
}

.iconContainer {
  width: 12px;
  height: 12px;
}
