@import './../../styles/index.scss';

.lastSection {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    grid-template-columns: 1fr 1fr;
  }

  @media screen and (max-width: $phone) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}

.logoText {
  font-size: 24px;
  line-height: 140%;

  @media screen and (max-width: $desktop) {
    font-size: 18px;
  }
}

.socialMedia {
  display: flex;
  gap: 44px;

  @media screen and (max-width: $desktop) {
    display: none;
  }
}

.socialLink {
  padding: 10px;
  justify-self: flex-end;
  font-size: 24px;
  line-height: 140%;
}

.linkContainer {
  display: flex;
  align-items: center;
  gap: 8px;
  justify-self: flex-end;
}

.linkText {
  font-size: 24px;
  line-height: 140%;
  @media screen and (max-width: $desktop) {
    font-size: 18px;
  }
}

.iconContainer {
  width: 24px;
  height: 24px;
  background-color: $c-dot;
  border-radius: $border-radius;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
