@import './../../styles/index.scss';

.swiper {
  height: 850px;
  display: flex;
  align-items: center;
  width: 100%;

  @media screen and (min-width: $between) and (max-width: 1700px) {
    height: 700px;
  }

  @media screen and (min-width: $desktop) and (max-width: $between) {
    height: 500px;
  }
  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    height: 450px;
  }
  @media screen and (min-width: 630px) and (max-width: $tablet) {
    height: 360px;
  }

  @media screen and (min-width: $phone) and (max-width: 630px) {
    height: 330px;
  }
  @media screen and (min-width: 400px) and (max-width: $phone) {
    height: 320px;
  }
  @media screen and (max-width: 400px) {
    height: 300px;
  }
}

.swiper-slide {
  position: relative;
  padding-top: 20px;
  width: 100%;
  max-width: 1252px;

  @media screen and (min-width: $between) and (max-width: 1700px) {
    height: 700px;
    max-width: 960px;
  }

  @media screen and (min-width: $desktop) and (max-width: $between) {
    height: 500px;
    max-width: 560px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    height: 450px;
    max-width: 500px;
  }
  @media screen and (min-width: 630px) and (max-width: $tablet) {
    height: 360px;
    max-width: 440px;
  }
  @media screen and (min-width: $phone) and (max-width: 630px) {
    height: 330px;
    max-width: 350px;
  }
  @media screen and (min-width: 400px) and (max-width: $phone) {
    height: 320px;
    max-width: 320px;
  }

  @media screen and (max-width: 400px) {
    height: 300px;
    max-width: 270px;
  }
}

.swiper-slide-next {
  opacity: 0.4;
}
