@import './../../styles/index.scss';

.titleSection {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 66px;

  @media screen and (max-width: 1280px) {
    flex-direction: column-reverse;
    align-items: end;
  }
}

// ! Description container
.descriptionContainer {
  // border: 1px solid red;
  height: 440px;
  width: 100%;
  max-width: 254px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  margin-top: 90px;

  @media screen and (min-width: $screen) and (max-width: 1600px) {
    height: 380px;
  }

  @media screen and (min-width: $between) and (max-width: $screen) {
    height: 300px;
    max-width: 230px;
  }

  @media screen and (min-width: 670px) and (max-width: $between) {
    flex-direction: row;
    max-width: 100%;
    height: 100%;
  }

  @media screen and (min-width: 520px) and (max-width: 670px) {
    flex-direction: column;
    gap: 10px;
    height: 100%;
    max-width: 100%;
  }

  @media screen and (max-width: 520px) {
    flex-direction: column;
    gap: 10px;
    height: 100%;
    max-width: 100%;
    align-items: center;
  }
}

.description {
  font-size: 24px;
  line-height: 140%;

  @media screen and (min-width: $screen) and (max-width: 1600px) {
    font-size: 20px;
  }

  @media screen and (max-width: $screen) {
    font-size: 16px;
  }
}

// ! Title container

.titleContainer {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1110px;
  margin-top: 75px;

  @media screen and (min-width: $screen) and (max-width: 1600px) {
    max-width: 960px;
  }

  @media screen and (min-width: 850px) and (max-width: $screen) {
    max-width: 740px;
  }

  @media screen and (min-width: 520px) and (max-width: 850px) {
    max-width: 445px;
  }

  @media screen and (max-width: 520px) {
    max-width: 270px;
    margin-left: auto;
    margin-right: auto;
  }
}

.title {
  font-size: 154px;
  font-weight: $fw-regular;
  letter-spacing: -2px;
  line-height: 100%;
  margin-top: 0;
  margin-bottom: 0;

  @media screen and (min-width: $screen) and (max-width: 1600px) {
    font-size: 134px;
  }

  @media screen and (min-width: 850px) and (max-width: $screen) {
    font-size: 104px;
  }

  @media screen and (min-width: 520px) and (max-width: 850px) {
    font-size: 64px;
  }

  @media screen and (max-width: 520px) {
    font-size: 40px;
  }
}

.first {
  display: block;
  text-align: left;
}

.second {
  display: block;
  text-align: right;
}

.third {
  display: block;
  text-align: center;
}

.titleDescription {
  position: absolute;
  top: 24px;
  right: 0;
  font-size: 24px;
  line-height: 140%;
  width: 100%;
  max-width: 396px;

  @media screen and (min-width: $screen) and (max-width: 1600px) {
    max-width: 284px;
    font-size: 20px;
  }

  @media screen and (min-width: 850px) and (max-width: $screen) {
    max-width: 256px;
    font-size: 16px;
  }

  @media screen and (min-width: 520px) and (max-width: 850px) {
    max-width: 190px;
    font-size: 14px;
    top: 2px;
  }

  @media screen and (max-width: 520px) {
    position: static;
    max-width: 190px;
    font-size: 14px;
    margin-bottom: 10px;
  }
}

.titleDescriptionItalic {
  font-weight: $fw-light;
  font-style: italic;
}
