@import './../../styles/index.scss';

.auditContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 120px;
}

.card {
  width: 100%;
  max-width: 1680px;
  padding: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 80px;

  @media screen and (max-width: $desktop) {
    flex-direction: column;
    padding: 24px 10px;
    gap: 40px;
  }
}

.description {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.littleTitleContainer {
  display: flex;
  gap: 16px;
  margin-bottom: 80px;

  @media screen and (max-width: $tablet) {
    margin-bottom: 40px;
  }
}

.iconContainer {
  width: 12px;
  height: 12px;
}

.littleTitle {
  letter-spacing: 0.15px;
  line-height: 140%;
}

.title {
  font-weight: $fw-regular;
  font-size: 96px;
  line-height: 110%;
  letter-spacing: -1.5px;
  margin-bottom: 64px;

  @media screen and (min-width: $screen) and (max-width: 1705px) {
    font-size: 70px;
  }

  @media screen and (min-width: $between) and (max-width: $screen) {
    font-size: 50px;
  }

  @media screen and (max-width: $between) {
    font-size: 36px;
    margin-bottom: 32px;
  }
}

.italic {
  position: relative;
  font-style: italic;
}

.circleIconContainer {
  position: absolute;
  top: 7px;
  left: -28px;
  width: 295px;
  height: 110px;
  z-index: -1;

  @media screen and (min-width: $screen) and (max-width: 1705px) {
      width: 225px;
      top: 0;
      left: -15px;
  }

  @media screen and (min-width: $between) and (max-width: $screen) {
      width: 140px;
      top: -20px;
      left: -10px;
  }

  @media screen and (max-width: $between) {
      width: 100px;
      top: -30px;
      left: -10px;
  }
}

.text {
  font-size: 24px;
  line-height: 140%;
  margin-bottom: 64px;

  @media screen and (min-width: $between) and (max-width: $screen) {
    font-size: 20px;
  }

  @media screen and (min-width: $phone) and (max-width: $between) {
    font-size: 18px;
  }

  @media screen and (max-width: $phone) {
    font-size: 16px;
    margin-bottom: 32px;
  }
}

.inputContainer {
  position: relative;

    @media screen and (max-width: $phone) {
    position: static;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
}

.input {
  width: 100%;
  border: 1px solid $c-text;
  font-size: 24px;
  line-height: 140%;
  border-radius: 64px;
  padding: 19px 32px;

  @media screen and (max-width: $between) {
    padding: 13px 32px;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 18px;
    padding: 16px 32px;
  }

    @media screen and (max-width: $phone) {
    font-size: 16px;
    padding: 13px 32px;
  }
}

.buttonContainer {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;

  @media screen and (max-width: $phone) {
    position: static;
  }
}

// ! Image

.imageContainer {
  width: 100%;
  max-width: 680px;

  @media screen and (min-width: $desktop) and (max-width: $screen) {
    max-width: 500px;
  }
}
