* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 0;
  overflow-x: hidden;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

body {
  font-family: $ff-main;
  font-weight: $fw-regular;
  font-size: 16px;
  color: $c-text;
}

a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

svg {
  width: 100%;
  height: 100%;
  vertical-align: middle;
}

.contentContainer {
  height: 100%; // допомагає, коли колір бекграунду не покриває весь контент внизу
  width: 100%;
  max-width: 1740px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
  // border: 1px solid red;

  @media screen and (max-width: 850px) {
    max-width: 1780px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (max-width: 415px) {
    max-width: 1800px;

    padding-left: 10px;
    padding-right: 10px;
  }
}

.wrapper {
  height: 100%;
  width: 100%;
  max-width: 1920px;
}

.sliderPadding {
    height: 100%; // допомагає, коли колір бекграунду не покриває весь контент внизу
  width: 100%;
  max-width: 1740px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 40px;
  padding-right: 40px;
  // border: 1px solid red;

  @media screen and (max-width: 850px) {
    max-width: 1780px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (max-width: 415px) {
    max-width: 1800px;

    padding-left: 10px;
    padding-right: 10px;
  }
}
