@import './../../styles/index.scss';

.workContainer {
  text-align: center;
}

.pretitleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-bottom: 504px;

  @media screen and (min-width: 1360px) and (max-width: 1760px) {
    margin-bottom: 370px;
  }

  @media screen and (min-width: 1000px) and (max-width: 1360px) {
    margin-bottom: 270px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1000px) {
    margin-bottom: 190px;
  }

  @media screen and (max-width: $tablet) {
    margin-bottom: 150px;
  }
}

.imageContainer {
  position: relative;
}

.imageContainer img {
  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 600px;
    object-position: -200px;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 600px;
    object-position: -340px;
  }

  @media screen and (max-width: $phone) {
    object-fit: cover;

    width: 100%;
    height: 600px;
    object-position: -470px;
    overflow: hidden;
  }
}

.italic {
  font-style: italic;
}

.dot {
  background-color: $c-text;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.pretitle {
  font-weight: $fw-medium;
  letter-spacing: 0.15px;
  line-height: 140%;
}

.title {
  width: 100%;
  font-size: 96px;
  line-height: 110%;
  letter-spacing: -1.5px;
  font-weight: $fw-regular;
  gap: 12px;
  margin-top: 56px;
  margin-bottom: 56px;
  position: absolute;
  top: -46%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1130px;

  @media screen and (min-width: 1360px) and (max-width: 1760px) {
    font-size: 80px;
    max-width: 940px;
    top: -44%;
  }

  @media screen and (min-width: 1000px) and (max-width: 1360px) {
    font-size: 60px;
    max-width: 680px;
    top: -46%;
  }

  @media screen and (min-width: $tablet) and (max-width: 1000px) {
    font-size: 40px;
    max-width: 440px;
    top: -35%;
  }

  @media screen and (min-width: 570px) and (max-width: $tablet) {
    font-size: 30px;
    max-width: 320px;
    top: -29%;
  }

  @media screen and (max-width: 570px) {
    font-size: 22px;
    max-width: 230px;
    top: -24%;
  }
}

.span {
  position: relative;
  z-index: 5;
  font-style: italic;
  font-weight: $fw-regular;
}

.iconContainer {
  top: 0;
  width: 417px;
  height: 119px;
  left: -20px;
  top: 4px;
  position: absolute;
  z-index: -1;

  @media screen and (min-width: 1360px) and (max-width: 1760px) {
    width: 340px;
    left: -12px;
    top: 0;
  }

  @media screen and (min-width: 1000px) and (max-width: 1360px) {
    width: 250px;
    left: -6px;
    top: -15px;
  }

  @media screen and (min-width: $tablet) and (max-width: 1000px) {
    max-width: 160px;
    left: -6px;
    top: -30px;
  }

  @media screen and (min-width: 570px) and (max-width: $tablet) {
    max-width: 120px;
    left: -6px;
    top: -38px;
  }

  @media screen and (max-width: 570px) {
    max-width: 90px;
    left: -4px;
    top: -44px;
  }
}
