// ? Fonts
// Font Families
$ff-main: 'Plus Jakarta Sans', sans-serif;
$ff-title: 'Big Shoulders Display', sans-serif;

// Font sizes
$fs-title: 32px;
$fs-main-title: 44px;

// Font weights
$fw-light: 300;
$fw-regular: 400;
$fw-medium: 500;
$fw-semi-bold: 600;
$fw-bold: 700;

// ? Line height
$line-height: 26px;
$line-height-small: 18px;

// ? Colors
$c-text: #1d201f;
$c-white: #fff;
$c-dot: #ff3c00;
$c-mobile-bg: rgba(1, 1, 1, 0.9);
$c-bg: #F0F0F0;
$c-bg-2: #F5F5F5;

// ? Shadows
$box-shadow: 8px 8px rgba(255, 0, 0, 0.5);

// ? Transition
$t-normal: 0.35s cubic-bezier(0.4, 0, 0.2, 1);

// ? Border radius
$border-radius: 40px;

// ? Media Queries: Breakpoints
$small: 320px;
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;
$between: 1280px;
$screen: 1400px;
$large: 1920px;
